import { BigNumber } from '@ethersproject/bignumber';

export interface INftAttribute {
  id: number;
  display_type: string;
  trait_type: string;
  value: any;
}

export interface INftStats {
  name: string;
  value: number;
}

export interface INftDetails {
  symbol: string;
  index: number;
  attributes: INftAttribute[];
  stats: INftStats[];
  description: string;
  image: string;
  name: string;
}

export async function loadNftDetailsFromUri(
  index: number,
  tokenId: BigNumber,
  tokenUri: string,
  traits: any
): Promise<INftDetails> {
  const tokenResponse = await fetch(tokenUri.replace('https://farmland-game.github.io/assets/', './'));
  var nft = (await tokenResponse.json()) as INftDetails;
  nft.image = nft.image.replace('https://farmland-game.github.io/assets/', './');
  nft.index = index;

  nft.stats = [];
  nft.stats.push({ name: 'Stamina', value: traits['trait1'].toNumber() });
  nft.stats.push({ name: 'Strength', value: traits['trait2'].toNumber() });
  nft.stats.push({ name: 'Speed', value: traits['trait3'].toNumber() });
  nft.stats.push({ name: 'Courage', value: traits['trait4'].toNumber() });
  nft.stats.push({ name: 'Intelligence', value: traits['trait5'].toNumber() });

  var maxAttId = Math.max.apply(
    Math,
    nft.attributes.map(a => {
      return a.id;
    })
  );

  if (traits['trait1'].toNumber() > 94) {
    nft.attributes.push({
      id: ++maxAttId,
      display_type: 'Stat',
      trait_type: 'Stat',
      value: 'Tank'
    });
  }

  if (traits['trait2'].toNumber() > 94) {
    nft.attributes.push({
      id: ++maxAttId,
      display_type: 'Stat',
      trait_type: 'Stat',
      value: 'Warrior'
    });
  }

  if (traits['trait3'].toNumber() > 94) {
    nft.attributes.push({
      id: ++maxAttId,
      display_type: 'Stat',
      trait_type: 'Stat',
      value: 'Lightning'
    });
  }

  if (traits['trait4'].toNumber() > 94) {
    nft.attributes.push({
      id: ++maxAttId,
      display_type: 'Stat',
      trait_type: 'Stat',
      value: 'Hero'
    });
  }

  if (traits['trait5'].toNumber() > 94) {
    nft.attributes.push({
      id: ++maxAttId,
      display_type: 'Stat',
      trait_type: 'Stat',
      value: 'Genius'
    });
  }

  return nft;
}
