import React, { Component } from 'react';
import { Subscription } from 'rxjs';
import { Assets, AssetType, IAssetConfig } from '../../../models/AssetConfig';
import { INftAttribute, INftDetails } from '../../../models/NftDetails';
import { EthereumService } from '../../../services/EthereumService';
import { MessagingService } from '../../../services/MessagingService';
import { NonFungibleAssetService } from '../Services/NonFungibleAssetService';

import '../Estate.scss';
import './Collectibles.scss';

import si1 from '../../../assets/avatars/3.png';
import si2 from '../../../assets/avatars/4.png';
import si3 from '../../../assets/avatars/7.png';
import si4 from '../../../assets/avatars/8.png';
import { ChainId } from '@uniswap/sdk';

import SwiperCore, { Navigation } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper-bundle.min.css';
import 'swiper/swiper.scss';

interface ICollectiblesProps {
  messagingService: MessagingService;
  ethereumservice: EthereumService;
}

interface ICollectiblesState {
  isLoaded: boolean;
  investAsset?: string;
  assets: NonFungibleAssetService[];
  showDetail: boolean;
  amountToMint: number;
}

class Collectibles extends Component<ICollectiblesProps, ICollectiblesState> {
  subscription: Subscription;
  walletAddress?: string;
  chainId?: number;
  displayAssetTypes = [AssetType.Collectible];
  equipTargetAssetsTypes = [AssetType.Crop];
  equipTarget?: IAssetConfig;

  farmerNftService?: NonFungibleAssetService;

  constructor(props: any) {
    super(props);

    this.state = {
      isLoaded: false,
      investAsset: '',
      assets: [],
      amountToMint: 1,
      showDetail: false
    };

    this.subscription = new Subscription();

    this.handleAmountToMint = this.handleAmountToMint.bind(this);
    this.mintCharacter = this.mintCharacter.bind(this);

    SwiperCore.use([Navigation]);
  }
  componentDidMount() {
    var eventSubscription = this.props.messagingService.events.subscribe(event => {
      this.refresh();
    });

    this.subscription.add(eventSubscription);
    this.refresh();
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  handleAmountToMint(e: any) {
    var amount = Math.min(20, Math.max(1, e.target.value));
    this.setState({ amountToMint: amount });
  }

  mintCharacter() {
    this.farmerNftService?.mint(this.state.amountToMint);
  }

  refresh() {
    if (
      this.props.ethereumservice.walletData.walletAddress &&
      this.props.ethereumservice.networkManager?.network?.chainId
    ) {
      if (
        this.props.ethereumservice.walletData.walletAddress !== this.walletAddress ||
        this.props.ethereumservice.networkManager!.network?.chainId !== this.chainId
      ) {
        this.walletAddress = this.props.ethereumservice.walletData.walletAddress;
        this.chainId = this.props.ethereumservice.networkManager!.network!.chainId;
        this.init();
      } else {
        this.update();
      }
    }
  }

  init() {
    if (
      this.props.ethereumservice.networkManager?.network &&
      this.props.ethereumservice.networkManager?.network?.assets.size > 0
    ) {
      let displayAssets = [...this.props.ethereumservice.networkManager!.network!.assets.values()].filter(config =>
        this.displayAssetTypes.includes(config.assetConstants!.assetType!)
      );

      let equipTargets = [...this.props.ethereumservice.networkManager!.network!.assets.values()].filter(config =>
        this.equipTargetAssetsTypes.includes(config.assetConstants!.assetType!)
      );

      if (equipTargets.length > 1) {
        throw new Error('Multiple crops not supported for equipping collectibles');
      }
      this.equipTarget = equipTargets[0];

      var assetUpdatePromises = displayAssets.map(ac => {
        var asset = new NonFungibleAssetService(
          ac,
          this.equipTarget!,
          this.props.messagingService,
          this.props.ethereumservice
        );

        if (asset.config.assetConstants === Assets.FARMER) {
          this.farmerNftService = asset;
        }

        return asset.update().then(() => {
          return asset;
        });
      });

      return Promise.all(assetUpdatePromises).then(assets => {
        this.setState({ assets: assets, isLoaded: true });
      });
    }
  }

  update() {
    this.state.assets.forEach(asset => {
      asset.update();
    });

    this.setState({ assets: this.state.assets });
  }

  render() {
    var characterCount = this.state.assets.reduce((p, c) => p + c.nfts.length, 0);
    return (
      <section id="wallet" className="is-vcentered">
        <div className="is-max-fullhd1">
          {!this.props.ethereumservice.walletData.isConnected && (
            <span
              className="column meme is-half has-text-centered mt-4"
              style={{ cursor: 'pointer' }}
              onClick={() => this.props.ethereumservice.connectWallet()}>
              <h3 className="sub-title py-3">Connect Wallet</h3>
            </span>
          )}

          {this.props.ethereumservice.walletData.isConnected && this.state.isLoaded && (
            <div className="is-flex is-flex-direction-column">
              {characterCount > 0 && this.renderAssets()}
              {characterCount <= 0 && this.renderSampleAssets()}
              <section className="my-6">
                <div className="container is-flex is-flex-direction-column">
                  <div className="columns is-centered">
                    <span className="column meme is-bordered is-half has-text-centered">
                      {characterCount > 0 && <h3 className="sub-title is-upper pt-3">Mint more farmland characters</h3>}
                      {characterCount <= 0 && (
                        <h3 className="sub-title is-upper pt-3">Mint your own farmland characters</h3>
                      )}
                      {!this.props.ethereumservice.networkManager?.network?.hasCollectibles && (
                        <React.Fragment>
                          <h3 className="sub-title m-4" style={{ color: 'var(--orange2)' }}>
                            Coming Soon!
                          </h3>
                          <h3 className="sub-title m-4">
                            {this.props.ethereumservice.networkManager?.network?.chainId === ChainId.MAINNET && (
                              <span>(ON Arbitrum)</span>
                            )}
                          </h3>
                        </React.Fragment>
                      )}
                      {this.props.ethereumservice.networkManager?.network?.hasCollectibles && (
                        <React.Fragment>
                          <div className="field mt-5">
                            <div className="control is-large">
                              <input
                                className="input is-action"
                                min="0"
                                max="20"
                                type="number"
                                style={{ width: '200px' }}
                                value={this.state.amountToMint}
                                onChange={this.handleAmountToMint}
                                placeholder="Amount to Mint (1-20)"
                              />
                            </div>
                          </div>
                          <button className="button mt-2 mb-3 is-action" onClick={this.mintCharacter}>
                            Mint Characters
                          </button>{' '}
                        </React.Fragment>
                      )}
                      {/* */}
                    </span>
                  </div>
                </div>
              </section>
            </div>
          )}
        </div>
      </section>
    );
  }

  renderAssets() {
    return (
      <React.Fragment>
        {this.state.assets.map(a => (
          <CollectibleSet
            key={a.config.contractAddress}
            equipTarget={this.equipTarget!}
            nftService={a}></CollectibleSet>
        ))}
      </React.Fragment>
    );
  }

  renderSampleAssets() {
    const gaby = this.newSample(
      'Gaby Schlather',
      si2,
      [
        { id: 0, display_type: '', trait_type: 'Special', value: 'Founding father' },
        { id: 1, display_type: '', trait_type: 'Image', value: 'Deus ex machina' }
      ],
      0
    );
    const kevin = this.newSample(
      'Kevin Molz',
      si4,
      [{ id: 0, display_type: '', trait_type: 'Image', value: 'Laser eyes' }],
      1
    );
    const mitch = this.newSample(
      'Mitch Drummey',
      si1,
      [{ id: 0, display_type: '', trait_type: 'Stat', value: 'Lightning' }],
      2
    );
    const judy = this.newSample('Judy Yopp', si3, [], 3);

    return (
      <React.Fragment>
        <Swiper navigation spaceBetween={10} slidesPerView="auto" centerInsufficientSlides>
          <SwiperSlide>
            <Character onActivateClick={() => {}} isApproved={true} nft={gaby} isSample={true}></Character>
          </SwiperSlide>
          <SwiperSlide>
            <Character onActivateClick={() => {}} isApproved={true} nft={kevin} isSample={true}></Character>
          </SwiperSlide>
          <SwiperSlide>
            <Character onActivateClick={() => {}} isApproved={true} nft={mitch} isSample={true}></Character>
          </SwiperSlide>
          <SwiperSlide>
            <Character onActivateClick={() => {}} isApproved={true} nft={judy} isSample={true}></Character>
          </SwiperSlide>
        </Swiper>
      </React.Fragment>
    );
  }

  newSample(name: string, img: any, attributes: INftAttribute[], index: number): INftDetails {
    return {
      symbol: 'FARMER',
      index: index,
      attributes: attributes,
      stats: [
        {
          name: 'Strength',
          value: Math.floor(Math.random() * 70 + 20)
        },
        {
          name: 'Speed',
          value: attributes.some(a => a.value === 'Lightning') ? 97 : Math.floor(Math.random() * 70 + 20)
        },
        { name: 'Stamina', value: Math.floor(Math.random() * 70 + 20) },
        { name: 'Intelligence', value: Math.floor(Math.random() * 70 + 20) },
        { name: 'Courage', value: Math.floor(Math.random() * 70 + 20) }
      ],
      description:
        attributes.length === 0
          ? 'A common Farmland Character'
          : attributes.length === 1
          ? 'An uncommon Farmland Character'
          : 'A rare Farmland Character',
      image: img,
      name: name
    };
  }
}

interface ICollectibleSetProps {
  nftService: NonFungibleAssetService;
  equipTarget: IAssetConfig;
}

class CollectibleSet extends Component<ICollectibleSetProps> {
  navigationPrevRef: any;
  navigationNextRef: any;

  constructor(props: any) {
    super(props);
    this.activate = this.activate.bind(this);

    this.navigationPrevRef = React.createRef();
    this.navigationNextRef = React.createRef();
  }

  async activate(nft: INftDetails) {
    if (this.props.nftService.isApproved) {
      this.props.nftService.activate(
        this.props.nftService.config.assetConstants!.assetTypeCode,
        nft,
        this.props.equipTarget
      );
    } else {
      this.props.nftService.approve(this.props.equipTarget, this.props.nftService.config);
    }
  }

  render() {
    return (
      <React.Fragment>
        <Swiper navigation spaceBetween={10} slidesPerView="auto" centerInsufficientSlides>
          {this.props.nftService!.nfts.map(n => (
            <SwiperSlide key={n.name + n.index}>
              <Character
                onActivateClick={this.activate}
                isApproved={this.props.nftService.isApproved}
                nft={n}
                isSample={false}></Character>
            </SwiperSlide>
          ))}
        </Swiper>
      </React.Fragment>
    );
  }
}

interface ICharacterProps {
  nft?: INftDetails;
  isApproved: boolean;
  onActivateClick: any;
  isSample: boolean;
}

class Character extends Component<ICharacterProps> {
  constructor(props: any) {
    super(props);
    this.activate = this.activate.bind(this);
  }

  activate() {
    this.props.onActivateClick(this.props.nft);
  }

  render() {
    return (
      <div className="stage mr-3 my-5 is-flex is-flex-direction-column is-align-items-center">
        <div
          className={`card p-3 ${this.rarityClass()}`}
          style={{
            backgroundImage: `linear-gradient(rgba(10, 10, 10, 0.6), rgba(10, 10, 10, 1)),url(${this.props.nft!.image})`
          }}>
          <div className="card-image">
            <figure className="image is-3by3">
              <img className="nft" src={this.props.nft!.image} alt="Farmland Collectible" />
              {this.props.isSample && <h3 className="nft-sample">Sample</h3>}
            </figure>
          </div>
          <div className="card-content p-0 py-2">
            <div className="content">
              <p className="title is-4 mb-0 mt-2">{this.props.nft!.name}</p>
              {/* <p className="is-size-6 mb-0">{this.props.nft!.description}</p> */}
              <p className="is-size-6 mb-0">{this.description()}</p>
              <div className="attributes">{this.renderAttributes()}</div>
              <div className="is-flex is-flex-direction-column is-align-items-end has-text-weight-bold mt-2">
                {this.props.nft!.stats.map(s => {
                  return (
                    <div key={s.name} className="is-flex is-justify-content-space-between">
                      <p className="is-size-7 mb-0">{s.name}</p>
                      <p className={`${this.statClass(s.value)} is-size-7 mb-0`}>{s.value}</p>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
        </div>
        <div className="stage-action">
          <button
            className="button is-transparent-action activate-button"
            onClick={this.activate}
            disabled={this.props.isSample}>
            <span>Activate</span>
          </button>
        </div>
      </div>
    );
  }

  description(): string {
    var specialAttributes = this.props.nft!.attributes.filter(
      a => ['Special', 'Image', 'Stat'].indexOf(a.trait_type) >= 0
    ).length;
    if (specialAttributes > 3) return 'A legendary Farmland character';
    if (specialAttributes > 2) return 'An epic Farmland character';
    if (specialAttributes > 1) return 'A rare Farmland character';
    if (specialAttributes > 0) return 'An uncommon Farmland character';
    return 'A common Farmland character';
  }

  rarityClass(): string {
    var specialAttributes = this.props.nft!.attributes.filter(
      a => ['Special', 'Image', 'Stat'].indexOf(a.trait_type) >= 0
    ).length;
    if (specialAttributes > 3) return 'rarity legendary';
    if (specialAttributes > 2) return 'rarity epic';
    if (specialAttributes > 1) return 'rarity rare';
    if (specialAttributes > 0) return 'rarity uncommon';
    return '';
  }

  statClass(value: number): string {
    if (value > 94) return 'super-stat';
    if (value > 79) return 'good-stat';
    return '';
  }

  renderAttributes() {
    return (
      <React.Fragment>
        {this.props
          .nft!.attributes.filter(a => ['Special', 'Image', 'Stat'].indexOf(a.trait_type) >= 0)
          .map(a => {
            return (
              <span key={a.id} className={`tag is-primary ${a.trait_type}`}>
                {a.value}
              </span>
            );
          })}
      </React.Fragment>
    );
  }
}

export default Collectibles;
